<template>
    <div class="home">
        <!---->
        <v-container>
            <v-row>
                <b></b>
            </v-row>
            <v-row>
                <a href="https://vuetifyjs.com/"></a>
            </v-row>
            <br/><br/>
            <v-row>
                <b></b>
            </v-row>
            <v-row>
                <!---->
                <v-col sm="3">
                    <v-btn @click="open_warning_msgbox" color="warning">
                        {{$t('testbench.warning_msgbox')}}
                    </v-btn>
                    <alert-messagebox 
                    :alertTitle="$t('testbench.warning_msgbox')"
                    :alertLevel="`warning`"
                    :alertBody="$t('testbench.warning_msgbox_content')"
                    ref="warning_msgbox_ref">
                    </alert-messagebox>
                </v-col>
                <!---->
                <v-col sm="3">
                    <v-btn @click="open_error_msgbox" color="error">
                        {{$t('testbench.error_msgbox')}}
                    </v-btn>
                    <alert-messagebox 
                    :alertTitle="$t('testbench.error_msgbox')"
                    :alertLevel="`error`"
                    :alertBody="$t('testbench.error_msgbox_content')"
                    ref="error_msgbox_ref">
                    </alert-messagebox>
                </v-col>
                <!---->
                <v-col sm="3">
                    <v-btn @click="open_success_msgbox" color="success">
                        {{$t('testbench.success_msgbox')}}
                    </v-btn>
                    <alert-messagebox 
                    :alertTitle="$t('testbench.success_msgbox')"
                    :alertLevel="`success`"
                    :alertBody="$t('testbench.success_msgbox_content')"
                    ref="success_msgbox_ref">
                    </alert-messagebox>
                </v-col>
                <!---->
                <v-col sm="3">
                    <v-btn @click="open_info_msgbox" color="blue">
                        {{$t('testbench.info_msgbox')}}
                    </v-btn>
                    <alert-messagebox 
                    :alertTitle="$t('testbench.info_msgbox')"
                    :alertLevel="`info`"
                    :alertBody="$t('testbench.info_msgbox_content')"
                    ref="info_msgbox_ref">
                    </alert-messagebox>
                </v-col>
            </v-row>
            <br/><br/>
            <v-row>
                <b>Toastification </b>
                
            </v-row>
            <v-row>
                <v-col sm="3">
                    <v-btn @click="open_warning_toast" color="warning">
                        {{$t('testbench.warning_toast')}}
                    </v-btn>
                </v-col>
                <v-col sm="3">
                    <v-btn @click="open_error_toast" color="error">
                        {{$t('testbench.error_toast')}}
                    </v-btn>
                </v-col>
                <v-col sm="3">
                    <v-btn @click="open_success_toast" color="success">
                        {{$t('testbench.success_toast')}}
                    </v-btn>
                </v-col>
                <v-col sm="3">
                    <v-btn @click="open_info_toast" color="info">
                        {{$t('testbench.info_toast')}}
                    </v-btn>
                </v-col>
            </v-row>
            <br/><br/>
            <v-row>
                <b></b>
            </v-row>
            <v-row>
                <v-col sm="3">
                    <v-btn @click="show_prog" color="success">
                        {{$t('testbench.show_prog')}}
                    </v-btn>
                    <pending-progress-card
                    ref="progress"
                    :zmsShow="show_progress"
                    :zmsPendingList="['','']"/>
                </v-col>
            </v-row>
        </v-container>

    </div>
</template>

<script>
import AlertMessagebox from '../CommonComponents/AlertMessagebox.vue';
import PendingProgressCard from '../CommonComponents/PendingProgressCard.vue';
export default {
    name:"TestBench",
    components:{
        AlertMessagebox,
        PendingProgressCard
    },
    data(){
        return{
            show_progress:false,
        }
    },
    methods:{
        open_warning_msgbox(){
            this.$refs.warning_msgbox_ref.showAlert();
        },
        open_error_msgbox(){
            this.$refs.error_msgbox_ref.showAlert();
        },
        open_success_msgbox(){
            this.$refs.success_msgbox_ref.showAlert();
        },
        open_info_msgbox(){
            this.$refs.info_msgbox_ref.showAlert();
        },
        open_warning_toast(){
            this.$store.dispatch("showToastNotify", {
                type: "warning",
                info: this.$t('testbench.warning_toast'),
            });
        },
        open_error_toast(){
            this.$store.dispatch("showToastNotify", {
                type: "error",
                info: this.$t('testbench.error_toast'),
            });
        },
        open_success_toast(){
            this.$store.dispatch("showToastNotify", {
                type: "success",
                info: this.$t('testbench.success_toast'),
            });
        },
        open_info_toast(){
            this.$store.dispatch("showToastNotify", {
                type: "default",
                info: this.$t('testbench.info_toast'),
            });
        },
        show_prog(){
            this.show_progress=true
            this.$refs.progress.reset()
            this.$refs.progress.complete(0)
            setTimeout(()=>{
                this.$refs.progress.complete(1)
                this.show_progress=false
            },2000)
        }
    }
}
</script>

<style>

</style>