<template>
  <div>
    <v-chart
      class="chart"
      :theme="this.$vuetify.theme.dark ? 'dark' : 'light'"
      :option="option"
      autoresize
    />
  </div>
</template>
<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { PieChart } from "echarts/charts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
} from "echarts/components";
import VChart from "vue-echarts";
use([
  CanvasRenderer,
  PieChart,
  TitleComponent,
  TooltipComponent,
  LegendComponent,
]);
export default {
  name: "ECPieCharts",
  components: {
    VChart,
  },
  provide: {},
  props: {
    tjChartTitle: String,
    tjChartLegend: Array,
    tjChartData: Array,
  },
  data() {
    return {
      option: {
        title: {
          text: "Traffic Sources",
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/> {b} : {c} ({d}%)",
        },
        legend: {
          orient: "vertical",
          left: "left",
          data: [
            "Direct",
            "Email",
            "Ad Networks",
            "Video Ads",
            "Search Engines",
          ],
        },
        series: [
          {
            name: "Traffic Sources",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: [
              { value: 335, name: "Direct" },
              { value: 310, name: "Email" },
              { value: 234, name: "Ad Networks" },
              { value: 135, name: "Video Ads" },
              { value: 1548, name: "Search Engines" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      },
    };
  },
  mounted() {
    let _this = this;
    window.onresize = function () {
      _this.option.resize();
    };
    this.applyChanges();
  },
  methods: {
    applyChanges() {
      this.option.title.text = this.tjChartTitle;
      this.option.legend.data = this.tjChartLegend;
      this.option.series[0].name = this.tjChartTitle;
      this.option.series[0].data = this.tjChartData;
    },
  },
};
</script>
<style scoped>
.chart {
  height: 400px;
}
</style>
