<template> 
  <div class="tjtrade-home-notice ">
    <alert-messagebox
      ref="error_done"
      :alert-title="$t('home.exit.title')"
      :alert-body="$t('home.exit.note')"
      :alert-level="`warning`"
      @alertConfirm="exitLogin"
    />
    <v-card
      :ripple="{ class: null }"
      elevation="10"
      class="tjtrade-card-noborder tjtrade-cardcolor-light-strip"
      :class="cardNightClass"
    >
      <div class="tjtrade-home-title2 tjtrade-home-body2">
        <v-icon>mdi-account-circle</v-icon> {{ $t("home.system_overview") }}
      </div>
      <div class="tjtrade-home-body">
        <br/>
        
      </div>
    </v-card>
  </div>
</template>
<script>
import AlertMessagebox from "../CommonComponents/AlertMessagebox.vue";
export default {
  name: "HomepageAccountOverview",
  components: { AlertMessagebox },
  props: {
    drawer: Boolean,
  },
  data: () => {
    return {
      avatarImg: null,
      dialog_logout: false,
      userInfo: {
        userid: "---",
        name: "---",
        permission: "---",
      },
    };
  },
  computed: {
    userNameX() {
      return localStorage.getItem("tjtradeBKName");
    },
    userIdX() {
      return localStorage.getItem("tjtradeBKId");
    },
    userPositionX() {
      if (localStorage.getItem("tjtradeBKPosition") === "Admin") {
        return this.$t('home.user_permission.admin');
      }
      return localStorage.getItem("tjtradeBKPosition") + " "+this.$t('home.user_permission.admin');
    },
    cardNightClass() {
      console.log("CARD NIGHT CLASS")
      return {
        "tjtrade-cardcolor-light-strip": !this.$vuetify.theme.dark,
        "tjtrade-cardcolor-dark-strip": this.$vuetify.theme.dark,
      };
    },
  },
  created() {
    let urlTemp = this.$store.state.sAsset_WelcomeIcon;
    this.avatarImg = require("@/" + urlTemp);
  },
  methods: {
    exitLogin() {
      localStorage.setItem("tjtradeToken", "");
      localStorage.setItem("tjtradeBKId", "");
      this.$router.push("/Login");
    },
    exitLoginCall() {
      this.$refs.error_done.showAlert();
    },
    exitLoginEmit() {
      this.$emit("exitLogin");
    },
  },
};
</script>
<style scoped lang="scss">
.tjtrade-home-notice {
  margin: 0px 20px 0px 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.tjtrade-home-center {
  text-align: center;
}
.tjtrade-card-noborder {
  border-width: 0px;
}
.tjtrade-home-title2 {
  font-size: 18px;
  font-weight: bold;
  display: block;
}
.tjtrade-home-body {
  margin-left: 7%;
  margin-right: 7%;
  padding-top: 10px;
  padding-bottom: 10px;
}
.tjtrade-home-body2 {
  margin-left: 5%;
  margin-right: 5%;
  padding-top: 20px;
  padding-bottom: 10px;
}
.tjtrade-img-demo {
  filter: brightness(19200%) saturate(0%);
}
</style>