<template>
  <div class="zms-anicare" :class="nmNightClass">

    <!---->
    <div class="zms-query-filter">
      <v-icon color="primary"> mdi-filter-plus </v-icon>
      <span class="zms-query-title"></span>
      <div>

        <!---->
        <v-container>
          <v-row>
            <v-col cols="12" sm="6" md="3">
              <v-text-field
                v-model="submitId"
                :label="$t('communication.view.id')"
                readonly
                prepend-icon="mdi-music-accidental-sharp"
              />
            </v-col>
            <v-col cols="12" sm="6" md="3">
              <v-text-field
                v-model="submitType"
                :label="$t('communication.view.title')"
                prepend-icon="mdi-tag"
              />
            </v-col>
            <v-col cols="12" sm="6" md="3">
              <v-text-field
                v-model="submitVetName"
                :label="$t('communication.view.recv')"
                prepend-icon="mdi-tag"
                readonly
              />
            </v-col>
            <v-col cols="12" sm="6" md="3">
              <v-menu
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                    v-model="date"
                    :label="$t('communication.view.time')"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  />
                </template>
                <v-date-picker
                  v-model="date"
                  color="primary"
                  width="400"
                  @input="menu2 = false"
                />
              </v-menu>
            </v-col>
          </v-row>
        </v-container>
        <v-container>
          <v-row>
            <v-spacer/>
            <v-col cols="12" sm="6" md="3">
              <v-btn
                v-ripple
                :disabled="queryLoaderDialog === true"
                block
                class="zms-width"
                color="error"
              >
                <v-icon>mdi-filter-minus</v-icon>&nbsp;&nbsp;{{$t('communication.view.remove_filter')}}
              </v-btn>
            </v-col>
            

            <v-dialog v-model="submitStat" persistent width="300">
              <v-card>
                <v-card-title> {{ $t("animalCare.Submitting") }} </v-card-title>
                <v-divider />
                <br />
                <v-card-text>
                  {{ $t("animalCare.PleaseWait") }} <br />
                  <br />
                  <v-progress-linear
                    indeterminate
                    striped
                    color="primary"
                    class="mb-0"
                  />
                </v-card-text>
              </v-card>
            </v-dialog>
            <v-col cols="12" sm="6" md="3">
              <v-btn
                v-ripple
                :disabled="queryLoaderDialog === true"
                block
                class="zms-width"
                color="primary"
              >
                <v-icon>mdi-filter</v-icon>&nbsp;&nbsp;{{$t('communication.view.find')}}
              </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </div>
    </div>
    <v-divider />
    <!---->
    <v-dialog v-model="errorReturn" persistent width="500">
      <v-card color="" :ripple="{ class: null }">
        <v-card-title
          class="zms-strip-bg text-h5 text--white red"
          color="warning"
        >
          <v-icon color="white"> mdi-close-thick </v-icon>&nbsp;<span
            class="text--white"
            style="color: #ffffff !important"
          >
            {{ errorTitle }}
          </span>
        </v-card-title>
        <v-divider />
        <br />
        <v-card-text>
          <span class="zms-poptip-body" :class="txNightClass">
            {{ errorInfo }}
          </span>
          <br />
          <br />
        </v-card-text>
        <v-divider />
        <v-card-actions>
          <v-spacer />
          <v-btn
            class="zms-fullwidth"
            v-bind="attrs"
            light
            color="primary"
            v-on="on"
            @click="errorReturn = false"
          >
            <v-icon>mdi-exclamation</v-icon> {{ $t("common.confirm") }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div class="zms-query-result">
      <v-icon color="primary"> mdi-note-search </v-icon>
      <span class="zms-query-title">{{$t('communication.view.find_result')}}</span> <br />
      <br />
      <v-fade-transition>
        <v-alert
          v-if="queryData.length === 0 || queryData === null"
          type="info"
          class="zms-force-nowrap-e"
          text
          border="left"
        >
          {{ $t("communication.view.noresult") }}
        </v-alert>
      </v-fade-transition>
      <div class="zms-query-result-table">
        <v-data-table
          :headers="headers"
          :items="queryData"
          :page.sync="page"
          :items-per-page="5"
          hide-default-footer
          class="elevation-1"
          @page-count="pageCount = $event"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-toolbar-title>{{$t('communication.view.find_result')}}</v-toolbar-title>
              <v-spacer />
              <v-dialog v-model="dialog" scrollable persistent max-width="700">
                <v-card class="zms-ignore-overflowx" :ripple="{ class: null }">
                  <v-card-title
                    class="zms-strip-bg text-h5 text--white primary"
                    color="warning"
                  >
                    <v-icon color="white"> mdi-lead-pencil </v-icon>&nbsp;
                    <span class="text--white" style="color: #ffffff !important">
                      {{ $t("animalCare2.traceSuccour") }}
                    </span>
                  </v-card-title>
                  <v-stepper
                    v-model="stepperCount"
                    class="zms-ignore-overflow"
                    flat
                    :value="stepperCount + 1"
                  >
                    <v-stepper-header flat>
                      <v-stepper-step :complete="stepperCount > 1" step="1">
                        {{ $t("animalCare2.alterStep1") }}
                      </v-stepper-step>
                      <v-divider />
                      <v-stepper-step :complete="stepperCount > 2" step="2">
                        {{ $t("animalCare2.alterStep2") }}
                      </v-stepper-step>
                      <v-divider />
                      <v-stepper-step :complete="stepperCount > 3" step="3">
                        {{ $t("animalCare2.alterStep3") }}
                      </v-stepper-step>
                    </v-stepper-header>
                    <div>
                      <v-stepper-items>
                        <!---->
                        <v-stepper-content step="2">
                          <v-icon color="primary"> mdi-pen </v-icon>
                          <span class="zms-query-title">
                            {{ $t("animalCare2.alterProcess") }}
                          </span>
                          <v-card-text class="zms-ignore-overflowx">
                            <v-container>
                              <v-row>
                                {{ $t("animalCare2.traceProcessDescription2") }}
                              </v-row>
                              <v-row>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['animalId']"
                                    disabled
                                    :label="$t('animalCare.animalId')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['diseaseName']"
                                    disabled
                                    :label="$t('animalCare.diseaseName')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['veterinaryId']"
                                    disabled
                                    :label="$t('animalCare.vetName')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['drug']"
                                    :label="$t('animalCare.drugName')"
                                  />
                                </v-col>
                              </v-row>
                            </v-container>
                          </v-card-text>
                          <v-divider />
                          <br />
                          <!---->
                          <v-icon color="primary"> mdi-pen </v-icon>
                          <span class="zms-query-title">
                            {{ $t("animalCare2.markComplete") }}
                          </span>
                          <br />
                          <br />
                          <v-alert
                            type="warning"
                            class="zms-force-nowrap-e"
                            text
                            border="left"
                          >
                            {{ $t("animalCare2.closeReqWarning") }}
                          </v-alert>
                          <v-card-text>
                            <v-container>
                              <v-row>
                                {{ $t("animalCare2.closeReqDesc2") }}
                              </v-row>
                              <v-row>
                                <v-col cols="12" sm="6" md="6">
                                  <v-menu
                                    v-model="menu3"
                                    :close-on-content-click="false"
                                    :nudge-right="40"
                                    transition="scale-transition"
                                    offset-y
                                    min-width="290px"
                                  >
                                    <template v-slot:activator="{ on, attrs }">
                                      <v-text-field
                                        v-model="cureDate"
                                        :label="$t('animalCare2.cureDate')"
                                        prepend-icon="mdi-calendar"
                                        readonly
                                        v-bind="attrs"
                                        v-on="on"
                                      />
                                    </template>
                                    <v-date-picker
                                      v-model="cureDate"
                                      color="primary"
                                      width="400"
                                      @input="menu3 = false"
                                    />
                                  </v-menu>
                                </v-col>
                              </v-row>
                            </v-container>
                          </v-card-text>
                          <!---->
                          <alert-messagebox
                            ref="closeReqMsgbox"
                            :alert-title="$t('animalCare2.closeReqMsgTitle')"
                            :alert-body="$t('animalCare2.closeReqMsgBody')"
                            :alert-level="`warning`"
                            @alertConfirm="submitCloseReq"
                          />
                          <pending-progress-card
                            :zms-show="completeSubmitWaitingBox"
                          />
                          <v-card-text>
                            <v-container>
                              <v-row>
                                <v-col cols="12" sm="6" md="4">
                                  <v-btn
                                    class="zms-strip-bg-slim"
                                    block
                                    light
                                    color="error"
                                    @click="calloutCloseReqMsgbox(0)"
                                  >
                                    <v-icon>mdi-close</v-icon>
                                    {{ $t("common2.abort") }}
                                  </v-btn>
                                </v-col>
                                <v-col cols="12" sm="6" md="4">
                                  <v-btn
                                    class="zms-strip-bg-slim"
                                    block
                                    light
                                    color="success"
                                    @click="calloutCloseReqMsgbox(1)"
                                  >
                                    <v-icon>mdi-check-bold</v-icon>
                                    {{ $t("common2.complete") }}
                                  </v-btn>
                                </v-col>
                              </v-row>
                            </v-container>
                          </v-card-text>
                        </v-stepper-content>
                        <!---->
                        <v-stepper-content step="3">
                          <v-icon color="primary"> mdi-information </v-icon>
                          <span class="zms-query-title">
                            {{ $t("animalCare2.processDone") }}
                          </span>
                          <br />
                          <br />
                          <v-alert
                            type="error"
                            class="zms-force-nowrap-e"
                            text
                            border="left"
                          >
                            {{ $t("animalCare2.completeWork") }}
                          </v-alert>
                          <v-card-text class="zms-ignore-overflowx">
                            <v-container>
                              <v-row>
                                {{ $t("animalCare2.traceProcessDescription3") }}
                              </v-row>
                              <v-row>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['animalId']"
                                    disabled
                                    :label="$t('animalCare.animalId')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['diseaseName']"
                                    disabled
                                    :label="$t('animalCare.diseaseName')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['veterinaryId']"
                                    disabled
                                    :label="$t('animalCare.vetName')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['drug']"
                                    disabled
                                    :label="$t('animalCare.drugName')"
                                  />
                                </v-col>
                                <v-col cols="12" sm="6" md="6">
                                  <v-text-field
                                    v-model="editedItem['isCured']"
                                    disabled
                                    :label="$t('animalCare2.currentStatus')"
                                  />
                                </v-col>
                              </v-row>
                            </v-container>
                          </v-card-text>
                        </v-stepper-content>
                      </v-stepper-items>
                    </div>
                  </v-stepper>
                  <v-card-actions>
                    <v-spacer />
                    <v-btn
                      class="zms-halfwidth3"
                      light
                      color="primary"
                      @click="close"
                    >
                      <v-icon>mdi-arrow-left</v-icon> {{ $t("common.cancel") }}
                    </v-btn>
                    <v-btn
                      class="zms-halfwidth3"
                      light
                      color="success"
                    >
                      <v-icon>mdi-check</v-icon> {{ $t("common.save") }}
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:[`item.actions`]="{ item }">
            <v-icon small class="mr-2" @click="editItem(item)">
              mdi-pencil
            </v-icon>
          </template>
        </v-data-table>
      </div>
      <div class="zms-query-pagination">
        <v-pagination v-model="page" :length="pageCount" />
      </div>
    </div>
  </div>
</template>
<script>

//
import AlertMessagebox from "../CommonComponents/AlertMessagebox.vue";
import PendingProgressCard from "../CommonComponents/PendingProgressCard.vue";

export default {
  name: "UserNotificationSentBox",
  components: { AlertMessagebox, PendingProgressCard },
  data()  {
    return {
      stepperCount: 2,
      queryLoaderDialog: false,
      headers: [
        { text: this.$t('communication.view.id'), value: "id" },
        { text: this.$t('communication.view.title'), value: "title" },
        { text: this.$t('communication.view.recv'), value: "recv" },
        { text: this.$t('communication.view.time'), value: "time" },

        { text: this.$t('communication.view.action'), value: "actions", sortable: false },
      ],
      submitStat: 0,
      errorReturn: false,
      errorTitle: "",
      submitType: null,
      errorInfo: "",
      pageCount: 0,
      menu2: 0,
      menu3: 0,
      on: 0,
      attrs: 0,
      page: 1,
      atpx: 0,
      cureDate: null,
      dialog: 0,
      date: null,
      queryData: [],
      editedIndex: -1,
      editedItem: {
        name: "",
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: "",
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      submitNote: "",
      submitId: null,
      submitVetName: null,
      completeSubmitWaitingBox: 0,
      completeMsgboxModel: 0,
      completeType: null,
      completeList: ["", ""],
    };
  },
  computed: {
    nmNightClass() {
      return {
        "zms-background-nm-dark": this.$vuetify.theme.dark,
      };
    },
    txNightClass() {
      return {
        "zms-text-dark": this.$vuetify.theme.dark,
      };
    },
  },
  created() {},
  methods: {
    //
    calloutCloseReqMsgbox(x) {
      this.atpx = x;
      this.$refs.closeReqMsgbox.showAlert();
    },
    //
    showcloseReqMsgbox() {
      this.$refs.closeReqMsgbox.showAlert();
    },
    //
    fetchCareInfo() {
      
    },
    //
    submitCloseReq() {
      
    },
    //
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    //
    editItem(item) {
      this.editedIndex = this.queryData.indexOf(item);
      if (item.isCured === "") {
        this.stepperCount = 2;
      } else {
        this.stepperCount = 3;
      }
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    //
    clearData() {
      this.submitId = null;
      this.submitVetName = null;
      this.submitType = null;
      this.date = null;
      this.$store.dispatch("showToastNotify", {
        type: "default",
        info: "",
      });
    },
    //
    submitCareInfo() {
      
    },
    //Toast
    submitSuccTip(x) {
      this.$store.dispatch("showToastNotify", { type: "success", info: x });
    },
    //Toast
    submitFailTip(x) {
      this.$store.dispatch("showToastNotify", { type: "error", info: x });
    },
  },
};
</script>

<style scoped lang="scss">
.zms-query-pagination {
  margin-top: 10px;
  transition: all 0.5s;
}
.zms-query-result {
  margin-top: 20px;
}
.zms-anicare {
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 20px;
  padding-bottom: 20px;
}
.zms-query-title {
  font-weight: bold;
  letter-spacing: 1px;
  position: relative;
  top: 3px;
}
.zms-query-result-table {
  margin-top: 10px;
}
.zms-ignore-overflow {
  overflow: scroll !important;
}
.zms-ignore-overflowx {
  overflow: hidden !important;
}
</style>