<template>
  <div class="zms-appbar-ext">
    <alert-messagebox
      ref="error_done"
      :alert-title="``"
      :alert-body="` ZMS `"
      :alert-level="`warning`"
      @alertConfirm="exitLogin"
    />
    <span class="zms-appbar-acc">  (1956444)</span>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="zms-margin-rt"
          color="primary"
          text
          v-bind="attrs"
          v-on="on"
        >
          <v-icon color="white" class="float-right" @click="goBack">
            mdi-arrow-left
          </v-icon>
        </v-btn>
      </template>
      
    </v-tooltip>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="zms-margin-rt"
          color="primary"
          text
          v-bind="attrs"
          v-on="on"
        >
          <v-icon color="white" class="float-right" @click="switchLock">
            mdi-lock
          </v-icon>
        </v-btn>
      </template>
      ZMS
    </v-tooltip>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="zms-margin-rt"
          color="primary"
          text
          v-bind="attrs"
          @click="exitLoginCall"
          v-on="on"
        >
          <v-icon color="white" class="float-right"> mdi-exit-to-app </v-icon>
        </v-btn>
      </template>
      ZMS
    </v-tooltip>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          stclass="zms-margin-rt"
          color="primary"
          text
          v-bind="attrs"
          @click="switchNightMode"
          v-on="on"
        >
          <v-icon color="white" class="float-right"> mdi-weather-night </v-icon>
        </v-btn>
      </template>
      
    </v-tooltip>
  </div>
</template>
<script>
import AlertMessagebox from "../CommonComponents/AlertMessagebox.vue";
export default {
  name: "AppbarExt",
  components: { AlertMessagebox },
  data() {
    return {
      locked: false,
    };
  },
  computed: {
    zmsLock() {
      return {
        "zms-hidden": this.locked == false,
      };
    },
    userNameX() {
      return localStorage.getItem("zmsBKName");
    },
    userIdX() {
      return localStorage.getItem("zmsBKId");
    },
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    exitLogin() {
      localStorage.setItem("zmsToken", "");
      localStorage.setItem("zmsBKId", "");
      this.$router.push("/Login");
    },
    exitLoginCall() {
      this.$refs.error_done.showAlert();
    },
    switchNightMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("zmsAppearance.darkMode", this.$vuetify.theme.dark);
    },
    switchLock() {
      this.$emit("lockPress");
    },
  },
};
</script>
<style scoped lang="scss">
.zms-hidden {
  display: none;
}
.zms-margin-rt {
  margin-right: 10px;
}
.zms-appbar-acc {
  font-weight: bold;
  color: #ffffff;
  margin-right: 15px;
}
.zms-lock-screen {
  position: fixed;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
}
</style>
