<template>
  <div class="tjtrade-home-notice">
    <alert-messagebox
      ref="error_done"
      :alert-title="$t('home.exit.title')"
      :alert-body="$t('home.exit.note')"
      :alert-level="`warning`"
      @alertConfirm="exitLogin"
    />
    <v-card
      :ripple="{ class: null }"
      elevation="10"
      class="tjtrade-card-noborder"
      :class="cardNightClass"
    >
      <div class="tjtrade-home-title2 tjtrade-home-body2">
        <v-icon>mdi-account-circle</v-icon> {{ $t("home.accountOv") }}
      </div>
      <div class="tjtrade-home-body">
        <v-container>
          <v-row>
            <v-col cols="3" style="padding-right: 10px">
              <v-avatar size="112" color="grey">
                <v-img
                  contain
                  :src="avatarImg"
                  aspect-ratio="16/9"
                  height="100px"
                  class="tjtrade-img tjtrade-img-demo"
                />
              </v-avatar>
            </v-col>
            <v-col cols="9">
              
                <b class="tjtrade-home-username" color="primary">  </b>
                <span class="tjtrade-home-userid"> 1956444 </span>
                <span>  </span> <br />
                <hr/>
                <span class="tjtrade-home-account-sub">
                    <span> IP  : 10.60.41.1</span> <br />
                    <span> : 20211221</span> <br />
                </span>
                <br />
                <v-btn class="zms-fullwidth" color="error" @click="exitLoginCall">
                    <v-icon>mdi-exit-to-app</v-icon>{{ $t("home.exit.btn") }}
                </v-btn>
                &nbsp;&nbsp;
                <v-btn class="zms-fullwidth" color="primary" @click="exitLoginCall">
                    <v-icon>mdi-pencil</v-icon>{{ $t("home.modifyBtn") }}
                </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </div>
    </v-card>
  </div>
</template>
<script>
import AlertMessagebox from "../CommonComponents/AlertMessagebox.vue";
export default {
  name: "HomepageAccountOverview",
  components: { AlertMessagebox },
  props: {
    drawer: Boolean,
  },
  data: () => {
    return {
      avatarImg: null,
      dialog_logout: false,
      userInfo: {
        userid: "---",
        name: "---",
        permission: "---",
      },
    };
  },
  computed: {
    userNameX() {
      return localStorage.getItem("tjtradeBKName");
    },
    userIdX() {
      return localStorage.getItem("tjtradeBKId");
    },
    userPositionX() {
      if (localStorage.getItem("tjtradeBKPosition") === "Admin") {
        return this.$t('home.user_permission.admin');
      }
      return localStorage.getItem("tjtradeBKPosition") + " "+this.$t('home.user_permission.admin');
    },
    cardNightClass() {
      return {
        "tjtrade-cardcolor-light": !this.$vuetify.theme.dark,
        "tjtrade-cardcolor-dark": this.$vuetify.theme.dark,
      };
    },
  },
  created() {
    let urlTemp = this.$store.state.sAsset_WelcomeIcon;
    this.avatarImg = require("@/" + urlTemp);
  },
  methods: {
    exitLogin() {
      localStorage.setItem("tjtradeToken", "");
      localStorage.setItem("tjtradeBKId", "");
      this.$router.push("/Login");
    },
    exitLoginCall() {
      this.$refs.error_done.showAlert();
    },
    exitLoginEmit() {
      this.$emit("exitLogin");
    },
  },
};
</script>
<style scoped lang="scss">
.tjtrade-home-notice {
  margin: 0px 20px 0px 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.tjtrade-home-center {
  text-align: center;
}
.tjtrade-card-noborder {
  border-width: 0px;
}
.tjtrade-home-title2 {
  font-size: 18px;
  font-weight: bold;
  display: block;
}
.tjtrade-home-body {
  margin-left: 7%;
  margin-right: 7%;
  padding-top: 10px;
  padding-bottom: 10px;
}
.tjtrade-home-body2 {
  margin-left: 5%;
  margin-right: 5%;
  padding-top: 20px;
  padding-bottom: 10px;
}
.tjtrade-img-demo {
  filter: brightness(19200%) saturate(0%);
}
</style>