<template>
  <div class="tjtrade-home-notice">
    <alert-messagebox
      ref="error_done"
      :alert-title="$t('home.exit.title')"
      :alert-body="$t('home.exit.note')"
      :alert-level="`warning`"
      @alertConfirm="exitLogin"
    />
    <v-card
      :ripple="{ class: null }"
      elevation="10"
      class="tjtrade-card-noborder"
      :class="cardNightClass"
    >
      <div class="tjtrade-home-title2 tjtrade-home-body2">
        <v-icon>mdi-account-circle</v-icon> {{ $t("home.gauge") }}
      </div>
      <div class="tjtrade-home-body">
        <v-container>
          <v-row>
            <v-col sm="6">
              <!---->
              <table>
                <tr>
                  <td align-self="stretch">
                    <v-progress-circular
                      :rotate="-90"
                      :size="140"
                      :width="15"
                      :value="pending_refund_arb_value"
                      color="primary"
                    >
                      <span class="tjtrade-gauge-num">
                        {{ pending_refund_arb }}
                      </span>
                    </v-progress-circular>
                  </td>
                  <td  align-self="stretch" class="tjtrade-gauge-desc-wrap-tr">
                    <div class="tjtrade-gauge-desc-wrap">
                      <b class="tjtrade-gauge-desc-title">
                        <span class="tjtrade-misc-sharp">#</span>
                        {{$t('home.gauge_detail.refund_arbitrations.title')}}
                      </b>
                      <hr/>
                      {{$t('home.gauge_detail.refund_arbitrations.desc')}}
                    </div>
                  </td>
                </tr>
              </table>
            </v-col>
            <v-col sm="6">
              <!---->
              <table>
                <tr>
                  <td align-self="stretch">
                    <v-progress-circular
                      :rotate="-90"
                      :size="140"
                      :width="15"
                      :value="pending_reports_value"
                      color="warning"
                    >
                      <span class="tjtrade-gauge-num">
                        {{ pending_reports }}
                      </span>
                    </v-progress-circular>
                  </td>
                  <td  align-self="stretch" class="tjtrade-gauge-desc-wrap-tr">
                    <div class="tjtrade-gauge-desc-wrap">
                      <b class="tjtrade-gauge-desc-title">
                        <span class="tjtrade-misc-sharp">#</span>
                        {{$t('home.gauge_detail.commodity_reports.title')}}
                      </b>
                      <hr/>
                      {{$t('home.gauge_detail.commodity_reports.desc')}}
                    </div>
                  </td>
                </tr>
              </table>
            </v-col>
          </v-row>
          <br/><br/>
          <v-row>
            <v-col sm="6">
              <!---->
              <table>
                <tr>
                  <td align-self="stretch">
                    <v-progress-circular
                      :rotate="-90"
                      :size="140"
                      :width="15"
                      :value="pending_commodity_publ_value"
                      color="success"
                    >
                      <span class="tjtrade-gauge-num">
                        {{ pending_commodity_publ }}
                      </span>
                    </v-progress-circular>
                  </td>
                  <td  align-self="stretch" class="tjtrade-gauge-desc-wrap-tr">
                    <div class="tjtrade-gauge-desc-wrap">
                      <b class="tjtrade-gauge-desc-title">
                        <span class="tjtrade-misc-sharp">#</span>
                        {{$t('home.gauge_detail.commodity_publ.title')}}
                      </b>
                      <hr/>
                      {{$t('home.gauge_detail.commodity_publ.desc')}}
                    </div>
                  </td>
                </tr>
              </table>
            </v-col>
            <v-col sm="6">
              <!---->
              <table>
                <tr>
                  <td align-self="stretch">
                    <v-progress-circular
                      :rotate="-90"
                      :size="140"
                      :width="15"
                      :value="pending_unread_msg_value"
                      color="error"
                    >
                      <span class="tjtrade-gauge-num">
                        {{ unread_msg }}
                      </span>
                    </v-progress-circular>
                  </td>
                  <td  align-self="stretch" class="tjtrade-gauge-desc-wrap-tr">
                    <div class="tjtrade-gauge-desc-wrap">
                      <b class="tjtrade-gauge-desc-title">
                        <span class="tjtrade-misc-sharp">#</span>
                        {{$t('home.gauge_detail.unread_msg.title')}}
                      </b>
                      <hr/>
                      {{$t('home.gauge_detail.unread_msg.desc')}}
                    </div>
                  </td>
                </tr>
              </table>
            </v-col>
          </v-row>
        </v-container>
      </div>
    </v-card>
  </div>
</template>
<script>
import AlertMessagebox from "../CommonComponents/AlertMessagebox.vue";
export default {
  name: "HomepageGauge",
  components: { AlertMessagebox },
  props: {
    drawer: Boolean,
  },
  data: () => {
    return {
      pending_reports:15,
      pending_refund_arb:29,
      pending_commodity_reports:90,
      pending_commodity_publ:233,
      unread_msg:14,
    };
  },
  computed: {
    cardNightClass() {
      return {
        "tjtrade-cardcolor-light": !this.$vuetify.theme.dark,
        "tjtrade-cardcolor-dark": this.$vuetify.theme.dark,
      };
    },
    pending_reports_value(){
      return parseInt(this.pending_reports/(this.pending_reports+60)*100);
    },
    pending_refund_arb_value(){
      return parseInt(this.pending_refund_arb/(this.pending_refund_arb+60)*100);
    },
    pending_commodity_reports_value(){
      return parseInt(this.pending_commodity_reports/(this.pending_commodity_reports+60)*100);
    },
    pending_commodity_publ_value(){
      return parseInt(this.pending_commodity_publ/(this.pending_commodity_publ+60)*100);
    },
    pending_unread_msg_value(){
      return parseInt(this.unread_msg/(this.unread_msg+60)*100);
    },
  },
};
</script>
<style scoped lang="scss">
.tjtrade-home-notice {
  margin: 0px 20px 0px 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.tjtrade-home-center {
  text-align: center;
}
.tjtrade-card-noborder {
  border-width: 0px;
}
.tjtrade-home-title2 {
  font-size: 18px;
  font-weight: bold;
  display: block;
}
.tjtrade-home-body {
  margin-left: 7%;
  margin-right: 7%;
  padding-top: 10px;
  padding-bottom: 10px;
}
.tjtrade-home-body2 {
  margin-left: 5%;
  margin-right: 5%;
  padding-top: 20px;
  padding-bottom: 10px;
}
.tjtrade-img-demo {
  filter: brightness(19200%) saturate(0%);
}
</style>