import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import TestBench from "../views/Testbench.vue"
import CommodityCensor from "../views/CommodityCensor.vue"
import CommodityReportProcess from "../views/CommodityReportProcess"
import RefundArbitration from "../views/RefundArbitration.vue"
import UserNotification from "../views/UserNotification.vue"

Vue.use(VueRouter);
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/testbench",
    name: "TestBench",
    component: TestBench,
  },
  {
    path:"/commodity/publishCensor",
    name:"CommodityCensor",
    component:CommodityCensor
  },
  {
    path:"/commodity/reportProcess",
    name:"CommodityReportProcess",
    component:CommodityReportProcess
  },
  {
    path:"/trade/refundArbitration",
    name:"RefundArbitration",
    component:RefundArbitration
  },
  {
    path:"/communication/notify",
    name:"UserNotification",
    component:UserNotification
  }


];
let router = new VueRouter({
  base: "/",
  routes,
});
router.beforeEach((to, from, next) => {
  let isAuthenticated = localStorage.getItem("zmsToken");
  if (isAuthenticated == "" || isAuthenticated == undefined) {
    isAuthenticated = false;
  } else {
    isAuthenticated = true;
  }/*
  if (to.name !== "Login" && !isAuthenticated) next({ name: "Login" });
  else if (to.name === "Login" && isAuthenticated) next({ name: "Home" });
  else next();*/
  next();
});
export default router;
